@import "./variables.scss";

@mixin wrapper {
  padding: 1.5rem;
}

@mixin grid($col, $mrg) {
  float: left;
  margin-right: $mrg;
  margin-bottom: $mrg;
  width: (100% - (($col - 1) * $mrg)) / $col;

  &:nth-child(#{$col}n) {
    margin-right: 0;
  }
}

@mixin titleContainer {
  padding: 1rem 0;

  h3 {
    text-transform: capitalize;
    color: $color--five;
    font-size: 1.3rem;
  }

  p {
    color: $color--six;
    font-size: 0.95rem;
    text-transform: capitalize;
  }
}

@mixin empty {
  margin: 10% auto;
  text-align: center;
  font-size: 0.8em;
  color: grey;
  font-weight: 600;
}

@mixin flexbox($hor, $ver) {
  display: flex;
  justify-content: $hor;
  align-items: $ver;
}

@mixin signFormContainer {
  background-color: white;
  min-height: 100vh;
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1000;
}
