@import "../../scss/mixins.scss";

.recipeDetails {
  transition: all 1s;

  .infoTitle {
    @include titleContainer;
  }

  .imageBg {
    width: 100%;
    height: 200px;
    border-radius: 3px;
    overflow: hidden;
    background-position: center;
    background-size: cover;
  }

  img {
    width: 100%;
    height: 100%;
  }

  .btnContainer {
    @include flexbox(space-between, center);
    margin: 0.5rem 0;
  }

  .price {
    font-size: 1.3em;
    padding-left: 6px;
  }

  .buttons {
    @include flexbox(center, center);
  }

  .fa-heart {
    margin-right: 1rem;
  }

  .red__heart {
    color: red;
  }
}

@media screen and (min-width: 500px) {
  .recipeDetails {
    width: 80vw;
    margin: auto;

    .imageBg {
      height: 320px;
    }
  }
}

@media screen and (min-width: 1125px) {
  .recipeDetails {
    width: 65vw;
    margin: auto;
  }
}
