@import '../../scss/variables.scss';
@import '../../scss/mixins.scss';

.snackbar {
  @include flexbox(center, center);
  padding: 16px;
  background-color: $primaryColor;
  border-radius: 2px;
  color: white;
  position: fixed;
  top: 5em;
  right: 2em;
  z-index: 1000;

  .text {
    font-size: 0.8em;
    padding-right: 12px;
    text-transform: capitalize;
  }

  .checkIcon {
    font-size: 0.8em;
    color: $secondaryColor;
  }
}